<template>
  <div id="app">
    <!-- 로그인이 되지 않았을 때는 로그인 UI만 보여줍니다. -->
    <div v-if="!authenticated" class="login-container">
      <h1 class="title">비밀번호 입력</h1>
      <input type="password" v-model="password" placeholder="비밀번호" class="password-input" />
      <button @click="checkPassword" class="login-button">입력</button>
      <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>
    </div>
    <!-- 로그인이 되었다면, router-view를 통해 현재 경로에 맞는 컴포넌트를 띄웁니다. -->
    <!-- JH_HOME은 라우터 설정을 통해 보여지도록 합니다. -->
    <div v-else>
      <router-view /> 
    </div>
  </div>    
</template>

<script>
// JH_HOME 컴포넌트 임포트 줄을 완전히 삭제해주세요!
// import JH_HOME from './components/JH_HOME.vue'; // 이 줄을 지우면 됩니다!

export default {
  name: "App",
  components: {
    // 이제 이 부분도 비어있는 채로 두시면 됩니다.
    // JH_HOME // 이 주석도 제거해버려도 괜찮아요!
  },
  data() {
    return {
      password: '',
      authenticated: false,
      errorMessage: ''
    };
  },
  methods: {
  checkPassword() {
    const correctPassword = '0719'; 
    if (this.password === correctPassword) {
      this.authenticated = true;
      this.errorMessage = '';
      this.$router.push('/hoonlog/'); // <-- 이 부분을 "/hoonlog/" 로 변경!
    } else {
      this.errorMessage = '비밀번호가 틀렸습니다.';
    }
  }
}
}
</script>

