<template>


<div class="bg-success p-2 text-white bg-opacity-75">
  <h1 class="title">
    <a >
      <router-link to ="/hoonlog/JH_Home">Home</router-link>
   </a>
  </h1>

   <div class="year-container">
      <button
        v-for="(year, index) in years"
        :key="index"
        @click="toggleList(index)"
        class="year-button"
      >
        {{ year.title }}
      </button>
    </div>

    <div v-for="(year, index) in years" :key="index">
      <ul v-if="year.showList" class="sublist">
        <li v-for="(item, subIndex) in year.items" :key="subIndex">
          <router-link :to="item.link">{{ item.name }}</router-link> <!-- router-link 추가 -->
        </li>
      </ul>
    </div>

    <!-- <ul>
      <router-link to ="/hoonlog/jhlist">♥2019♥</router-link>
    </ul>
    <ul>
      <router-link to ="/hoonlog/jhlist2">♥2020♥</router-link>
    </ul> -->
  </div>

</template>

<script>

export default {
  name: 'JH_Home',
  data() {
    return {
      years: [
        { title: '2019', items: [{ name: '♥2019♥', link: '/hoonlog/jhlist' }], showList: false },
        { title: '2020', items: [{ name: '♥2020♥', link: '/hoonlog/jhlist2' }], showList: false },
        { title: '2021',
          items: [{ name: '♥2021 03♥', link: '/hoonlog/jhlist3' }, { name: '♥2021 10♥', link: '/hoonlog/jhlist4' }, { name: '♥2021 11♥', link: '/hoonlog/jhlist5' }], showList: false },
        { title: '2022',
          items: [{ name: '♥2022 1분기♥', link: '/hoonlog/jhlist6' }, { name: '♥2022 속초 (4월)♥', link: '/hoonlog/jhlist7' }, { name: '♥2022 부여 (4월)♥', link: '/hoonlog/jhlist8' },
          { name: '♥2022 2분기♥', link: '/hoonlog/jhlist9' },{ name: '♥2022 북서울꿈의숲 (6월)♥', link: '/hoonlog/jhlist10' },{ name: '♥2022 속초 (8월)♥', link: '/hoonlog/jhlist11' },
          { name: '♥2022 3분기♥', link: '/hoonlog/jhlist12' },{ name: '♥2022 경주 (10월)♥', link: '/hoonlog/jhlist13' },{ name: '♥2022 아쿠아리움  (12월)♥', link: '/hoonlog/jhlist14' },
          { name: '♥2022 12♥', link: '/hoonlog/jhlist15' }], showList: false },
        { title: '2023',
          items: [{ name: '♥2023 1♥', link: '/hoonlog/jhlist16' }, { name: '♥2023 2,3♥', link: '/hoonlog/jhlist17' }, { name: '♥2023 논산 명재 고택 (벚꽃구경)♥', link: '/hoonlog/jhlist18' },
          { name: '♥2023 논산 명재 고택 (벚꽃구경) - 추가♥', link: '/hoonlog/jhlist19' },{ name: '♥2023 봄 (4,5)♥', link: '/hoonlog/jhlist20' },
          { name: '♥2023 부여 유채꽃밭♥', link: '/hoonlog/jhlist21' }, { name: '♥2023 여름 (7~9)♥', link: '/hoonlog/jhlist22' },
          { name: '♥2023 가을 (어린이대공원)♥', link: '/hoonlog/jhlist23' },{ name: '♥2023 청남대♥', link: '/hoonlog/jhlist24' },
          { name: '♥2023 11♥', link: '/hoonlog/jhlist25' },{ name: '♥2023 12♥', link: '/hoonlog/jhlist26' }], showList: false },
      ]
    };
  },
  methods: {
    toggleList(index) {
      this.years[index].showList = !this.years[index].showList;
      // 상태가 변경될 때마다 localStorage에 저장!
      localStorage.setItem('yearShowListState', JSON.stringify(this.years.map(year => year.showList)));
    },
    // 컴포넌트가 로드될 때 localStorage에서 상태를 불러오는 메서드
    loadListState() {
      const savedState = localStorage.getItem('yearShowListState');
      if (savedState) {
        const parsedState = JSON.parse(savedState);
        this.years.forEach((year, index) => {
          if (parsedState[index] !== undefined) {
            year.showList = parsedState[index];
          }
        });
      }
    }
  },
  // 컴포넌트가 마운트될 때 (처음 로드되거나 다시 로드될 때) 상태를 불러옵니다.
  mounted() {
    this.loadListState();
  }
};

</script>

<style>
.title{
  text-align: center; /* 제목 중앙 정렬 */
  margin-bottom: 20px; /* 제목과 아래 요소 사이 여백 */
}
.year-container {
  display: flex; /* 가로 방향 정렬 */
  justify-content: center; /* 수평 중앙 정렬 */
  gap: 10px; /* 버튼 간격 */
  margin-bottom: 20px;
}
.year-button {
  padding: 10px;
  cursor: pointer;
}
.sublist {
  margin-left: 20px; /* 서브리스트 왼쪽 여백 */
}
img{
  padding:10%;
  max-width: 100%;
}
.router-link {
  color: white; /* 기본 링크 색상: 하얀색 */
  text-decoration: none; /* 밑줄 제거 */
}

.router-link-active {
  color: green; /* 활성화된 링크 색상: 초록색 */
}
</style>